/* AreaChart.css */
/* Styles for the AreaChart componet */

p{
  margin:2px;
}

.areachart-container{
  display: flex;
  justify-content: center;
}

.areachart-svg{
  width:100%;
}

.focusCircle {
    transition: fill 0.1s ease;
}

.focusLine {
  fill: none;
  stroke: steelblue;
  stroke-width: 0.5px;
}

.focusCircle:hover {
  cursor:pointer;
  stroke:black;
  stroke-width:1;
  opacity: 1;
}

.box {
  fill: none;
  pointer-events: all;
}

.tooltip {
    position: absolute;
    text-align: left;
    width: auto;
    height: auto;
    padding: 5px;
    font: 16px sans-serif;
    color: black;
    background-color: rgb(255,255,255,0.9);    
    border-width: 3px;
    border-style: outset;
    border-radius: 10px;
    border-color:#197a7b
    /* box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.4); */
    /* pointer-events: none; */
}

.show {
    visibility: visible;
    opacity: 1;
    transition: visibility 1s, opacity 1s;
}

.hide {
    visibility: hidden;
    opacity: 0;
    transition: visibility 1s, opacity 1s;
}
